html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h2 {
  margin: 0;
}

@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
    margin-bottom: 1.875rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --background: hsl(233, 47%, 7%);
  --card-background: hsl(244, 38%, 16%);
  --soft-violet:hsl(277, 64%, 61%);
  --main-heading-stats: hsl(0, 0%, 100%);
  --main-paragraph:hsla(0, 0%, 100%, 0.75);
  --stat-heading:hsla(0, 0%, 100%, 0.6);
}

:root {
  --font-inter: "Inter",
  sans-serif;
  --font-lexend: "Lexend Deca",
  sans-serif;
}

.container {
  max-width: 69.375rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.attribution {
  margin-bottom: 1.25rem;
}

body {
  background-color: var(--background);
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.container .card {
  background-color: var(--card-background);
  display: flex;
  flex-direction: row;
  border-radius: 0.625rem;
  gap: 1.25rem;
}
@media (max-width: 39.9375em) {
  .container .card {
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-around;
    max-width: 20.3125rem;
  }
}
.container .card__left {
  width: 50%;
  padding: 3.125rem 4.375rem 3.125rem 5rem;
}
@media (max-width: 39.9375em) {
  .container .card__left {
    width: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    padding: 1.25rem;
  }
}
.container .card__left .card__heading {
  color: var(--main-heading-stats);
}
@media (max-width: 39.9375em) {
  .container .card__left .card__heading {
    font-size: 1.6875rem;
    padding: 0 !important;
  }
}
.container .card__left .card__heading span {
  color: #aa5cdb;
}
.container .card__left .card__paragraph {
  color: var(--main-paragraph);
  font-size: 0.75rem;
  line-height: 1.1875rem;
}
@media (max-width: 39.9375em) {
  .container .card__left .card__paragraph {
    padding: 1rem;
    font-size: 0.875rem;
    line-height: 1.1875rem;
  }
}
.container .card__left .card__stats {
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  padding-top: 2.5rem;
}
@media (max-width: 39.9375em) {
  .container .card__left .card__stats {
    flex-direction: column;
    gap: 1.25rem;
  }
}
.container .card__left .card__stats .stats {
  list-style: none;
  color: var(--main-paragraph);
}
.container .card__left .card__stats .stats h2 {
  color: var(--stat-heading) !important;
}
.container .card__right {
  background: url("/images/image-header-desktop.jpg"), var(--soft-violet);
  width: 50%;
  max-height: 27.875rem;
  background-size: cover;
  border-radius: 0 0.625rem 0.625rem 0;
}
@media (max-width: 39.9375em) {
  .container .card__right {
    min-height: 15rem;
    min-width: 20.3125rem;
    background: url("/images/image-header-mobile.jpg");
    background-size: cover;
  }
}

.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: #3e52a3;
}